/* =========================================================
   Hero Planes Animation — LTR + RTL
   - Arce distincte (sus/inversate), fără intersecții de benzi
   - Unghiuri calibrate: 90° = înainte (dreapta LTR / stânga RTL)
     * LTR urcare:   82–88°  | LTR coborâre:   92–98°
     * RTL urcare:  262–268° | RTL coborâre:  272–278°
   - Toate pleacă OFF-screen (-12vw / 112vw) și ies OFF-screen
   - Offset Y ajustabil: var(--plane-offset-y, 0vh)
   - Durate recomandate (viteze diferite) la final
   ========================================================= */

/* ========== UPWARD ARCS (fly UP in middle) — LTR ========== */
/* Benzile sunt distanțate vertical ca să NU se intersecteze cu cele inversate */
/* Path 0: foarte jos, arc ușor în sus */
@keyframes fly-path-0 {
    0%   { transform: translate(-12vw, calc(80vh + var(--plane-offset-y, 0vh))) rotate(84deg); }
    25%  { transform: translate(20vw,  calc(76vh + var(--plane-offset-y, 0vh))) rotate(88deg); }
    50%  { transform: translate(50vw,  calc(74vh + var(--plane-offset-y, 0vh))) rotate(90deg); }
    75%  { transform: translate(80vw,  calc(76vh + var(--plane-offset-y, 0vh))) rotate(92deg); }
    100% { transform: translate(112vw, calc(80vh + var(--plane-offset-y, 0vh))) rotate(96deg); }
}

/* Path 1: jos-moderat, urcare vizibilă */
@keyframes fly-path-1 {
    0%   { transform: translate(-12vw, calc(68vh + var(--plane-offset-y, 0vh))) rotate(83deg); }
    25%  { transform: translate(20vw,  calc(62vh + var(--plane-offset-y, 0vh))) rotate(87deg); }
    50%  { transform: translate(50vw,  calc(58vh + var(--plane-offset-y, 0vh))) rotate(90deg); }
    75%  { transform: translate(80vw,  calc(62vh + var(--plane-offset-y, 0vh))) rotate(93deg); }
    100% { transform: translate(112vw, calc(68vh + var(--plane-offset-y, 0vh))) rotate(97deg); }
}

/* Path 2: mediu-jos, urcare mare */
@keyframes fly-path-2 {
    0%   { transform: translate(-12vw, calc(56vh + var(--plane-offset-y, 0vh))) rotate(82deg); }
    25%  { transform: translate(20vw,  calc(48vh + var(--plane-offset-y, 0vh))) rotate(86deg); }
    50%  { transform: translate(50vw,  calc(40vh + var(--plane-offset-y, 0vh))) rotate(90deg); }
    75%  { transform: translate(80vw,  calc(48vh + var(--plane-offset-y, 0vh))) rotate(94deg); }
    100% { transform: translate(112vw, calc(56vh + var(--plane-offset-y, 0vh))) rotate(98deg); }
}

/* Path 3: mediu, urcare foarte mare */
@keyframes fly-path-3 {
    0%   { transform: translate(-12vw, calc(44vh + var(--plane-offset-y, 0vh))) rotate(81deg); }
    25%  { transform: translate(20vw,  calc(32vh + var(--plane-offset-y, 0vh))) rotate(86deg); }
    50%  { transform: translate(50vw,  calc(24vh + var(--plane-offset-y, 0vh))) rotate(90deg); }
    75%  { transform: translate(80vw,  calc(32vh + var(--plane-offset-y, 0vh))) rotate(94deg); }
    100% { transform: translate(112vw, calc(44vh + var(--plane-offset-y, 0vh))) rotate(99deg); }
}

/* Path 4: sus, arc blând în sus */
@keyframes fly-path-4 {
    0%   { transform: translate(-12vw, calc(24vh + var(--plane-offset-y, 0vh))) rotate(86deg); }
    25%  { transform: translate(20vw,  calc(20vh + var(--plane-offset-y, 0vh))) rotate(88deg); }
    50%  { transform: translate(50vw,  calc(16vh + var(--plane-offset-y, 0vh))) rotate(90deg); }
    75%  { transform: translate(80vw,  calc(20vh + var(--plane-offset-y, 0vh))) rotate(92deg); }
    100% { transform: translate(112vw, calc(24vh + var(--plane-offset-y, 0vh))) rotate(94deg); }
}

/* ========== INVERTED ARCS (dip DOWN in middle) — LTR ========== */
/* Path 5: mediu, arc inversat clar */
@keyframes fly-path-5 {
    0%   { transform: translate(-12vw, calc(38vh + var(--plane-offset-y, 0vh))) rotate(96deg); }
    25%  { transform: translate(20vw,  calc(44vh + var(--plane-offset-y, 0vh))) rotate(92deg); }
