/* =========================================================
   Durate recomandate (viteze diferite) — opțional
   Aplicați pe elementele care folosesc keyframe-urile de mai sus.
   Ideea: căi mai lungi => durate mai mari. Niciun path nu se intersectează
   datorită benzilor verticale alese (16–80vh cu “goluri” între grupe).
   ========================================================= */
/* Exemple:
.plane--path-0 { animation: fly-path-0  24s linear infinite; }
.plane--path-1 { animation: fly-path-1  23s linear infinite; }
.plane--path-2 { animation: fly-path-2  22s linear infinite; }
.plane--path-3 { animation: fly-path-3  21s linear infinite; }
.plane--path-4 { animation: fly-path-4  20s linear infinite; }

.plane--path-5 { animation: fly-path-5  22s linear infinite; }
.plane--path-6 { animation: fly-path-6  21s linear infinite; }
.plane--path-7 { animation: fly-path-7  23s linear infinite; }
.plane--path-8 { animation: fly-path-8  24s linear infinite; }
.plane--path-9 { animation: fly-path-9  20s linear infinite; }

.plane--path-0-rtl { animation: fly-path-0-rtl  24s linear infinite; }
...
*/

/* Tips:
   - Pentru densitate mai mică pe mobil: scădeți dimensiunea iconului și creșteți ușor duratele.
   - Respectați prefers-reduced-motion: dezactivați animația la nevoie.
   - Dacă doriți “perfect constant speed”, setați duratele după lățimea secțiunii:
     dur ≈ (dist_hor_px / v_px_s) * factor_arc (1.00–1.15), unde factor_arc compensează curburile.
*/


/* Hero plane base styling */
.hero-plane {
  position: absolute;
  top: 0;
  left: 0;
  width: 32px;
  height: 32px;
  opacity: 0.4;
  will-change: transform;
  contain: layout paint style; /* Paint containment for performance */
  filter: drop-shadow(0 0 2px rgba(255, 255, 255, 0.3)) brightness(0) invert(1); /* White in dark mode */
}

/* Smaller planes on mobile for less visual noise */
@media (max-width: 640px) {
  .hero-plane {
    width: 24px;
    height: 24px;
    opacity: 0.35;
  }
}

/* Static positioning for reduced motion */
.hero-plane-static {
  position: absolute;
  width: 32px;
  height: 32px;
  opacity: 0.4;
  filter: drop-shadow(0 0 2px rgba(255, 255, 255, 0.3)) brightness(0) invert(1); /* White in dark mode */
}

@media (max-width: 640px) {
  .hero-plane-static {
    width: 24px;
    height: 24px;
    opacity: 0.35;
  }
}

/* Light mode: black planes */
.light .hero-plane {
  filter: drop-shadow(0 0 2px rgba(0, 0, 0, 0.15)) brightness(0); /* Black in light mode */
}

.light .hero-plane-static {
  filter: drop-shadow(0 0 2px rgba(0, 0, 0, 0.15)) brightness(0); /* Black in light mode */
}

/* Reduced motion: disable all plane animations */
@media (prefers-reduced-motion: reduce) {
  .hero-plane {
    animation: none !important;
  }
}


/* Product Pulse Animation for Search Highlight */
@keyframes productPulse {
  0% {
    transform: scale(1);
    box-shadow: 0 0 0 0 rgba(56, 189, 248, 0.45);
  }
  40% {
    transform: scale(1.02);
    box-shadow: 0 0 0 12px rgba(56, 189, 248, 0);
  }
  60% {
    transform: scale(1.015);
    box-shadow: 0 0 0 6px rgba(56, 189, 248, 0.15);
  }
  100% {
    transform: scale(1);
    box-shadow: 0 0 0 0 rgba(56, 189, 248, 0);
  }
}

.pulse-5s {
  animation: productPulse 5s ease-in-out 1;
}

/* Light Mode Theme Overrides */
.light {
  /* CSS Variables for Light Mode */
  --bg: #ffffff;
  --bg-muted: #f8fafc;
  --card: #ffffff;
  --card-muted: #f9fafb;
  --border: #e5e7eb;
  --shadow: 0 1px 2px rgba(0,0,0,.06), 0 2px 8px rgba(0,0,0,.06);

  --text: #0f172a;
  --text-muted: #475569;
  --text-subtle: #64748b;

  --primary: #0a84ff;
  --primary-fore: #ffffff;
  --accent: #111827;
  --link: #1d4ed8;
  --link-hover: #1e40af;

  --focus: #2563eb;

  /* Text colors */
  --tw-prose-body: #374151;
  --tw-prose-headings: #111827;
  --tw-prose-links: #3b82f6;

  /* Dropdown components (Light Mode - WCAG AA Compliant) */
  --dropdown-bg: #ffffff;
  --dropdown-text: #0f172a;           /* slate-900 - 16.37:1 contrast */
  --dropdown-border: #9ca3af;         /* gray-400 - visible but subtle */
  --dropdown-hover-bg: #f3f4f6;       /* gray-100 */
  --dropdown-focus-ring: #2563eb;     /* blue-600 */

  --dropdown-placeholder: #6b7280;    /* gray-500 - 4.61:1 contrast */
  --dropdown-caret: #374151;          /* gray-700 */

  --dropdown-menu-bg: #ffffff;
  --dropdown-menu-border: #d1d5db;    /* gray-300 */
  --dropdown-menu-shadow: 0 10px 15px -3px rgba(0, 0, 0, 0.1), 0 4px 6px -4px rgba(0, 0, 0, 0.05);

  --dropdown-option-default: #0f172a;        /* slate-900 */
  --dropdown-option-hover-bg: #f3f4f6;       /* gray-100 */
  --dropdown-option-hover-text: #0f172a;     /* slate-900 */
  --dropdown-option-selected-bg: #2563eb;    /* blue-600 */
  --dropdown-option-selected-text: #ffffff;
  --dropdown-option-disabled: #9ca3af;       /* gray-400 */
}

